@use 'base/mixins' as m;
@use 'base/variables' as v;
.header{
    min-height: 60rem;
    background-image: url(../img/grafico.svg), url(../img/grafico.svg), linear-gradient(to right, rgba(v.$negro, 0.9), rgba(v.$negro, 0.4)),
    image-set(url(../img/header.avif) 1x, url(../img/header.webp) 1x, url(../img/header.jpg) 1x);
    background-repeat: no-repeat;
    background-size: 400px, 400px, cover, cover;
    background-position: top left, bottom right, center, center;
    &__contenedor{
        @include m.contenedor(140rem);
    }
    &__navegacion{
        padding: 5rem 0;
        display: flex;
        justify-content: center;
        gap: 2rem;
        @include m.tablet {
            justify-content: flex-end;
        }
    }
    &__enlace{
        @include m.enlace;
    }
    &__contenido{
        padding-top: 5rem;
    }
    &__logo{
        @include m.texto-fluido(1rem, 8rem);
        @include m.texto-degradado(v.$azul, v.$secundario);
        text-align: center;
        @include m.tablet {
            text-align: left;
            display: inline-block;
        }
    }
    &__texto{
        color: v.$blanco;
        font-size: 4.4rem;
        font-weight: v.$bold;
        text-transform: uppercase;
        text-align: center;
        @include m.tablet {
            text-align: left;
        }
        &--modalidad{
           font-size: 3.4rem; 
        }
    }
    &__boton{
        @include m.boton(v.$azul, v.$blanco);
        border-radius: 1rem;
        text-transform: uppercase;
        @include m.tablet {
            display: inline;
            padding-left: 10rem;
            padding-right: 10rem;
        }
    }
}